import * as React from "react"
import { Link, graphql } from "gatsby"
import Seo from "../components/seo"
import { GatsbyImage } from "gatsby-plugin-image"


const BlogIndex = ({ data, location }) => {
  const siteTitle = data.site.siteMetadata?.title || `Title`
  return (
    <div id="LinkBox">
      <span>Links</span>
      <ul>
        <Link to="/blog/">
          Blog
        </Link>
        <Link to="https://www.linkedin.com/in/%EC%9E%AC%ED%98%B8-%EC%9D%B4-527ba2263/">
          LinkedIn
        </Link>
        <Link to="https://github.com/ljho01/">
          GitHub
        </Link>
        <Link to="https://www.instagram.com/ljh3567/">
          Instagram
        </Link>
        <Link to="https://soundcloud.com/lee1jaeho/">
          SoundCloud
        </Link>
      </ul>
    </div>
  )
}

export default BlogIndex

/**
 * Head export to define metadata for the page
 *
 * See: https://www.gatsbyjs.com/docs/reference/built-in-components/gatsby-head/
 */
export const Head = () => <Seo title="Links"/>

export const pageQuery = graphql`
  {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { frontmatter: { date: DESC } }) {
      nodes {
        excerpt(pruneLength: 100)
        fields {
          slug
        }
        frontmatter {
          date(formatString: "MMMM DD, YYYY")
          title
          description
          category
          thumbnail{
            childImageSharp{
              gatsbyImageData
            }
          }
        }
      }
      totalCount
      group(field: { frontmatter: { category: SELECT }}) {
        fieldValue
        totalCount
      }
    }
  }
`